.page-espacios {
  .header {

    &__espacio-logo {
      width: 80px;
    }

    &__espacio-titulo {
      h3 {
        color: white;
        font-family: 'nexa', sans-serif;
        letter-spacing: 2px;
        font-size: 28px;
        line-height: 100%;
        margin-top: 12px;

      }
    }

    &__sizes {
      display: flex;
    }

    &__size {
      width: 30px;
      height: 30px;
      line-height: 32px;
      margin-right: 12px;
      text-align: center;


      transition: all .3s ease-in-out;

      &.active {
        background: white;
        span {
          color: #000;
        }
      }
      &:last-child {
        margin-right: 0;
      }
      span {
        font-size: 20px;
        font-weight: 400;
        color: white;
      }

      &:hover {
        cursor: pointer;
        background-color: #C1CE00 !important;
        transform: scale(1.1);
      }
    }

    &__filtros {
      button {
        background: #fff;
        padding: 10px 24px;
        color: #808080;
        font-size: 12px;
        letter-spacing: 2px;
        font-family: 'nexa', sans-serif;
        text-transform: uppercase;
      }
    }

    .header__logo {
      position: absolute;
      right: 0;
      top: 0;
    }

    @media screen and (max-width:  575px){
      .container-fluid {
        flex-wrap: wrap;
        padding-bottom: 7px;
      }

      .header__icon,.header__espacio {
        margin-top: 10px;
      }

      .header__opciones {
        flex: 1 0 100%;
        margin-top: 10px;
        justify-content: space-between;
      }

      .header__sizes {
        margin-left: 0 !important;
      }


      .header__filtros {

        button {
          background: #fff;
          padding: 7px 12px;
          color: #808080;
          font-size: 11px;
          letter-spacing: 2px;
        }
      }

      &__espacio-titulo {
        h3 {
          font-size: 18px;
        }
      }

      .header__logo {
        right: 15px;
        img {
          height: 50px;
        }
      }

    }
  }

}

.bg--AZ {

  .header {
    background-color: #236293 !important;
    .container-fluid {
      background-color: #236293 !important;
    }
  }


  .espacio__menu {
    ul.nav {
      .nav-item.active,
      .nav-item:hover {
        background-color: #236293 !important;
        cursor: pointer;
        .nav-link {
          color: #fff !important;
        }

      }
    }
  }


}

.bg--NA {

  .header {
    background-color: #e97721 !important;
    .container-fluid {
      background-color: #e97721 !important;
    }
  }

}

.bg--CAF {

  .header {
    background-color: #bf7200 !important;
